footer {
  width: 100%;
  position: relative;
  background-color: #032B2F;
  padding-top: 40px;
  color: #fff;

  h1 {
    font-size: 1.6rem;
    text-align: center;
    font-weight: 700;
    margin-bottom: 40px;
  }

  .profile_photo {
    width: 125px;
    height: 125px;
    overflow: hidden;
    background-color: #fff;
    margin: 0 auto;
    border-radius: 90px;

    img {
      width: 125px;
    }
  }

  .footer_contact {
    width: 300px;
    margin: 0 auto;
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.8rem;
    font-weight: 700;

    img {
      width: 25px;
    }

  }
  .end {
    width: 100%;
    height: 50px;
    background-color: #2F393A;
    margin-top: 40px;

    p {
      font-size: 0.4rem;
      text-align: center;
      line-height: 50px;
    }
  }
}

@media screen and (min-width: 640px) {
  footer {
    .footer_contact {
      width: 500px;
      font-size: 1.2rem;
    }
    .end {
      p {
        font-size: 0.6rem;
      }
    }
  }
}
